import React,{useReducer} from 'react';
import ReactDOM from 'react-dom';
import {Route,BrowserRouter as Router,Switch,Redirect,Link} from 'react-router-dom'
import { UserContext } from './context/UserContext';
import { Login } from './components/Login';
import { UserLoginForm } from './components/User/UserLoginForm';
import { VendorLoginForm } from './components/Vendor/VendorLoginForm';
import { Signup } from './components/Signup';
import { UserSignupForm } from './components/User/UserSignupForm';
import { VendorSignupForm } from './components/Vendor/VendorSignupForm';
import { UserDashboard } from './components/User/UserDashboard';
import { VendorDashboard } from './components/Vendor/VendorDashboard';
import { VendorList } from './components/Vendor/VendorList';
import { Nav } from './components/NavBar';


const initialState = {
	'Login':false,
	'Iam':null,
	'username':'',
	'ID':'',
	'email':''
}



const reducer = (state,action) => {
	if(action.type === 'LOGIN'){
		let newState = {'Login':true}
		if(action.payload){
			newState['username'] = action.payload.username || null
			newState['email'] = action.payload.email || null
			newState['Iam'] = action.payload.Iam || null
		}
		return {...state,...newState}
	}
	if(action.type === 'LOGOUT'){
		return {...state,'Login':false}
	}
	return state;
}

function App(){
	
	const [state,dispatch] = useReducer(reducer,initialState)
    
	
	return(
	<UserContext.Provider  value={{state,dispatch}}>
		<Router>
			<Nav />
		   {state.Login?(
				(state.Iam === 'USER')?<Redirect from='/' to='/user/dashboard' />
					:
				(state.Iam === 'VENDOR')?<Redirect from='/' to='/vendor/dashboard' />:null
				):<Redirect from='/' to='/login' />}
				
				<Switch>
					<Route exact path='/login' component={Login} />
					<Route exact path='/user/login' component={UserLoginForm} />
					<Route exact path='/vendor/login' component={VendorLoginForm} />
					<Route exact path='/user/dashboard' component={state.Login?UserDashboard:UserLoginForm} />
					<Route exact path='/vendor/dashboard' component={state.Login?VendorDashboard:VendorLoginForm} />
					<Route exact path='/vendor/list' component={state.Login?VendorList:VendorLoginForm} />
					<Route exact path='/signup' component={Signup} />
					<Route exact path='/user/signup' component={UserSignupForm} />
					<Route exact path='/vendor/signup' component={VendorSignupForm} />
					<Route  path='*' component={NotFound} />
				</Switch>
		</Router>
	 </UserContext.Provider >
	)
}

ReactDOM.render(<App />,document.getElementById('root'));


if (module.hot) {
	module.hot.accept();
}




function NotFound(){
	return(
		<h1>I am Not Found Component</h1>
	)
}


