import React from 'react';
import {Link} from 'react-router-dom'
import { Button } from 'react-materialize';


export function Signup(){
	return(
	<div className="container center">
		<h1>Create an Account !!</h1>
		 <Button node="button"
			className="#e65100 orange darken-2"
			style={{
			  marginRight: '5px'
			}}
			waves="light"
		  >
			<Link to='/user/signup' style={{color: 'white'}} >
			I am Customer
			</Link>
		  </Button>
		  <Button node="button"
		  className="#e65100 orange darken-2"
			style={{
			  marginLeft: '10px'
			}}
			waves="light"
		  >
			<Link to='/vendor/signup' style={{color: 'white'}} >
				I am ShopeKeeper
			</Link>
		  </Button>
	</div>

	)
}