import React from 'react';
import {Link} from 'react-router-dom';
import {Table} from 'react-materialize';

export function VendorList({list}){
	
	const TableRows = list && list.map(el => {
		return (
		<tr>
			<td>{el.Title}</td>
			<td>{el.Description}</td>
			<td>{el.Price}</td>
		</tr>
		)
	})
	
	return(
	<div className="container center">
		<Table>
		  <thead>
			<tr>
			  <th data-field="id">
				Title
			  </th>
			  <th data-field="name">
				Description
			  </th>
			  <th data-field="price">
				Price
			  </th>
			</tr>
		  </thead>
		  <tbody>
			{TableRows}
		  </tbody>
		</Table>
	</div>
	)
}