import React,{useReducer,useContext} from 'react';
import {Link,useHistory} from 'react-router-dom';
import { Navbar,Icon,NavItem } from 'react-materialize';
import { UserContext } from '../context/UserContext';


export function Nav(){
	
	let history = useHistory();
	
	const {state,dispatch} = useContext(UserContext)
	
	function handleLogout(){
		dispatch({type:'LOGOUT'});
		history.push('/login')
	}
	
	return(
		<Navbar alignLinks="left" id="mobile-nav" 
			 brand={<div className="brand-logo logo-image right" href="#"><img id='logo' className="logo-image" src="assets/Tiffinia_icon_small.png"/></div>}
			 menuIcon={<Icon>menu</Icon>}
			 options={{
				draggable: false,
				edge: 'left',
				inDuration:100,
				onCloseEnd: null,
				onCloseStart: null,
				onOpenEnd: null,
				onOpenStart: null,
				outDuration: 100,
				preventScrolling: true
			  }}
			  
		>
		{state.Login?(
			<NavItem >
				<button type='button' className="btn" onClick={() => handleLogout()}>Logout</button>
			</NavItem>
		):null}
		{!state.Login?(
			<NavItem >
				<Link to='/login'>Login</Link>
			</NavItem>
		):null}
		{!state.Login?(
			<NavItem >
				<Link to='/signup'>Signup</Link>
			</NavItem>
		):null}
		
		
		
	</Navbar>
	)
}