import React,{useState} from 'react';
import {Formik,Form,Field,ErrorMessage} from 'formik';
import {useHistory,Link} from 'react-router-dom';

export  function UserSignupForm(){
	let history = useHistory();
	
	
	return(
	<div className="container center">
		<Formik 
				initialValues={{
					username:'',
					email:'',
					password:''
				}}
				
				onSubmit={(values,actions) => {
					fetch("/user/signup",{
						method:"POST",
						headers:{"Content-Type":"application/json"},
						body:JSON.stringify({...values})
					})
					.then((res) => {
						console.log(res)
						console.log(res.ok)
						if(res.ok){
							alert('Ok')
							history.push('/user/login')
						}else{
							alert('errors')
						}
						
					})
					.catch(() => {
						alert('Error');
					})
					.finally(() => {
						actions.setSubmitting(false)
					})
				}}
				
				validate={values => {
					const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
					const errors = {};
					if(!values.username){
						errors.username = 'Please Enter a Username'
					}
					if(!values.email || !emailRegex.test(values.email)){
						errors.email = 'Please Enter a Valid Email'
					}
					if(!values.password){
						errors.password = 'Please Enter a Password'
					}
					return errors;
				}}
			>
		{() => (
			<Form className='col s12'>
			  <div className='row'>
					<div className='input-field col s12'>
						<Field name="username" placeholder="Username" className='form-control' />
						
					</div>
					<div className='input-field col s12'>
						<Field name="email"  placeholder="Email" className='form-control' />
						
					</div>
					<div className='input-field col s12'>
						<Field name="password" placeholder="Password" className='form-control' />
					</div>	
					<p style={{color: 'red'}} ><ErrorMessage  name="username" /></p>
					<p style={{color: 'red'}}  ><ErrorMessage   name="email" /></p>
					<p style={{color: 'red'}} ><ErrorMessage   name="password" /></p>
					
			 </div>
			<div className='Btn' style={{textAlign:'center'}}>
						<button type='submit' className='btn btn-primary'>Submit</button>
			</div>
			</Form>
		)}
		</Formik>
	</div>
	)
}
