import React,{useState} from 'react';
import {Formik,Form,Field,ErrorMessage} from 'formik';
import {useHistory} from 'react-router-dom';


export  function VendorSignupForm(){
	let history = useHistory();
 
	
	return(
	<div className="container center">
		<Formik 
				initialValues={{
					username:'',
					mobile:'',
					password:''
				}}
				
				onSubmit={(values,actions) => {
					fetch("/vendor/signup",{
						method:"POST",
						headers:{"Content-Type":"application/json"},
						body:JSON.stringify({...values})
					})
					.then((res) => {
						console.log(res)
						console.log(res.ok)
						if(res.ok){
							history.push('/vendor/login')
						}else{
							alert('Failed')
						}
						
					})
					.catch(() => {
						alert('Error');
					})
					.finally(() => {
						actions.setSubmitting(false)
					})
				}}
				
				validate={values => {
					const errors = {};
					if(!values.password){
						errors.password = 'Please Enter Password'
					}
					if(!values.mobile){
						errors.email = 'Please Enter a Valid Mobile Number'
					}
					if(!values.username){
						errors.email = 'Please Enter a Username'
					}
					return errors;
				}}
			>
		{() => (
			<Form className='col s12'>
			  <div className='row'>
					<div className='input-field col s12'>
						<Field name="username" type="text" placeholder="Username" className='form-control' />
					</div>
					<div className='input-field col s12'>
						<Field name="mobile" type="number" placeholder="Mobile Number" className='form-control' />
					</div>
					<div className='input-field col s12'>
						<Field name="password"  type="password" placeholder="Password"  className='form-control'/>
					</div>
					<p style={{color: 'red'}} ><ErrorMessage  name="mobile" /></p>
					<p style={{color: 'red'}} ><ErrorMessage  name="password" /></p>
					<p style={{color: 'red'}} ><ErrorMessage  name="username" /></p>
			   </div>
				<div className='Btn' style={{textAlign:'center'}}>
					<button type='submit' className='btn btn-primary'>Submit</button>
				</div>
			</Form>
		)}
		</Formik>
	</div>
	)
}
