import React,{useContext,useState} from 'react';
import {Formik,Form,Field,ErrorMessage} from 'formik';
import { UserContext } from '../../context/UserContext';


export function VendorLoginForm(){
	
	const ctx = useContext(UserContext);
	
	const [status,SetStatus] = useState(0);
	
	return(
	<div className="container center">
		<Formik 
				initialValues={{
					mobile:'',
					password:'',
				}}
				
				onSubmit={(values,actions) => {
					fetch('/vendor/login',{
						method:"POST",
						headers:{"Content-Type":"application/json"},
						body:JSON.stringify({...values})
					})
					.then((res) => {
						if(res.ok){
							res.json().then(data => {
									console.log('Login Form Succesfully Recieved Data')
									console.log(data)
									ctx.dispatch({type:'LOGIN',payload:{'username':data.username,'Iam':'VENDOR'}})
							})	
						}
						else{
							console.log('Login Form Recieved Error')
							res.json().then(err => alert(err.msg))
						}
					})
					.catch(() => {
						alert('Error');
					})
					.finally(() => {
						actions.setSubmitting(false)
					})
				}}
				
				validate={values => {
					const errors = {};
					if(!values.password){
						errors.password = 'Please Enter Password'
					}
					if(!values.mobile){
						errors.email = 'Please Enter a Valid Mobile Number'
					}
					return errors;
				}}
			>
		{() => (
			<Form className='col s12'>
			  <div className='row'>
				<div className='input-field col s12'>
					<Field name="mobile" type="number" placeholder="Mobile Number" className='form-control' />
				</div>
				<div className='input-field col s12'>
					<Field name="password"  type="password" placeholder="Password" className='form-control'/>
				</div>
				<p style={{color: 'red'}} ><ErrorMessage  name="mobile" /></p>
				<p style={{color: 'red'}} ><ErrorMessage  name="password" /></p>
			 </div>
			 <div className='Btn' style={{textAlign:'center'}}>
				{(status === 0)?<button type='submit' className='btn btn-primary'>Submit</button>:null}
				{(status === 2)?<p style={{color: 'red'}} >Please Check Your Credentials</p>:null}
				{(status === 1)?<p style={{color: 'green'}} >You will be soon Redirected to Dashboard</p>:null}	
			</div>
			</Form>
		)}
		</Formik>
	</div>
	)
}